import { Link } from 'react-router-dom';

function Navbar() {
  return (
    <nav className="bg-primary text-white p-4 shadow-md">
      <div className="container mx-auto flex justify-between items-center">
        <Link to="/" className="text-2xl font-bold"></Link>
        <div className="space-x-4">
          <Link to="/" className="hover:underline"></Link>
          <Link to="/health-info" className="hover:underline"></Link>
          <Link to="/health-log" className="hover:underline"></Link>
          <Link to="/healthy-recipes" className="hover:underline"></Link>
          <Link to="/hospital-map"></Link>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
