import React, { useState, useEffect, useRef } from 'react';
import { GoogleMap, LoadScript } from '@react-google-maps/api';

const MapComponent = () => {
  // 
  const [currentPosition, setCurrentPosition] = useState({ lat: 23.973875, lng: 120.982024 });

  // 
  const [hospitalLocations, setHospitalLocations] = useState([]);

  // 
  const [locationError, setLocationError] = useState(false);

  // 
  const mapRef = useRef(null);

  // 
  const mapStyles = {
    height: '80vh',
    width: '100%',
  };

  //  
  const onMapLoad = (map) => {
    mapRef.current = map;

    //   PlacesService 
    const service = new window.google.maps.places.PlacesService(map);

    const request = {
      location: currentPosition,
      radius: 5000, //  5 
      type: ['hospital'], // 
    };

    //  
    service.nearbySearch(request, (results, status) => {
      if (status === window.google.maps.places.PlacesServiceStatus.OK) {
        setHospitalLocations(results); // 
      } else {
        console.error(':', status);
      }
    });

    //   AdvancedMarkerElement
    const userMarker = new window.google.maps.marker.AdvancedMarkerElement({
      map: map,
      position: currentPosition,
      title: '',
    });
  };

  //   hospitalLocations 
  useEffect(() => {
    if (!mapRef.current || hospitalLocations.length === 0) return;

    // 
    hospitalLocations.forEach((hospital) => {
      new window.google.maps.marker.AdvancedMarkerElement({
        map: mapRef.current,
        position: hospital.geometry.location,
        title: hospital.name,
      });
    });
  }, [hospitalLocations]);

  //  
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setCurrentPosition({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
          setLocationError(false);
        },
        (error) => {
          console.error(':', error.message);
          setLocationError(true);
        }
      );
    } else {
      console.error('');
      setLocationError(true);
    }
  }, []);

  return (
    <div>
      <h2 style={{ textAlign: 'center', margin: '1rem 0' }}> </h2>
      {locationError && (
        <p style={{ textAlign: 'center', color: 'red' }}>
          
        </p>
      )}

      {/*  libraries  */}
      <LoadScript
        googleMapsApiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY}
        libraries={['places']} // 
      >
        <GoogleMap
          mapContainerStyle={mapStyles}
          center={currentPosition}
          zoom={14}
          onLoad={onMapLoad}
        >
          {/*  onMapLoad  useEffect  AdvancedMarkerElement  */}
        </GoogleMap>
      </LoadScript>
    </div>
  );
};

export default MapComponent;